import React from 'react'
import { Link } from 'react-router-dom'
import './classLink.css'

const ClassLink = () => {
  return (
    <div className="container classLink">
      <div className="row">
        <h5 className="scorehvy col s12 myomColor">MYOM </h5>
        <Link to='/thema' className="col xl4 l4 m12 s12">
          <div className="card">
            <div className="card-image">
              <img src="/img/classLink/.jpg" alt="  "/>
              <span className='scorehvy'> </span>
            </div>
          </div>
        </Link>

        <Link to='/productIntro' className="col xl4 l4 m12 s12">
          <div className="card">
            <div className="card-image">
              <img src="/img/classLink/.jpg" alt="  "/>
              <span className='scorehvy'> </span>
            </div>
          </div>
        </Link>

        <Link to='/community/admin' className="col xl4 l4 m12 s12">
          <div className="card">
            <div className="card-image">
              <img src="/img/classLink/.jpg" alt="  "/>
              <span className='scorehvy'></span>
            </div>
          </div>
        </Link>

        <div className="divider col s6 offset-s3"></div>
      </div>
    </div>
  )
}

export default ClassLink;